import {Injectable} from '@angular/core';
import {
  Router, Resolve,
  RouterStateSnapshot,
  ActivatedRouteSnapshot
} from '@angular/router';
import {Observable, of} from 'rxjs';
import {OrderListService} from '../../Services/OrderServices/OrderListService/order-list.service';
import {IMainResponse} from '../../Models/Interfaces/Main/MainResponse';

@Injectable({
  providedIn: 'root'
})
export class OrderDetailsResolver implements Resolve<IMainResponse> {
  /**
   * The Requirements For The Construction Of The resolver
   * @param orderService for OrderListService
   */
  constructor(private orderService: OrderListService) {
  }
  /**
   * get order details
   * @param route for ActivatedRouteSnapshot
   * @return Observable<IMainResponse>
   */
  resolve(route: ActivatedRouteSnapshot): Observable<IMainResponse> {
    const id = +route.params.orderId;
    return this.orderService.getOrderById(id);
  }
}
